export default {
  // 
  namespaced: true,

  // 
  state: () => ({
        todayCheckCount:0
  }),

  // 
  mutations: {
    updateCheckCount(state,num){
      console.log("vuex");
      state.todayCheckCount=num
      console.log('vuextodaycheckCount:'+state.todayCheckCount);
    }

  },

  getters: {
    getTodayCheckCount(state){
      return state.todayCheckCount
    }
  }
}
